import React from 'react';
import {useEffect} from 'react';

import {makeStyles} from '@material-ui/core/styles';

import {Card, CardHeader, Divider, CardContent, Grid, Button, OutlinedInput, InputAdornment} from '@material-ui/core';

import {gridSpacing} from '../../store/constant';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import {IconSearch} from '@tabler/icons';

import axios from 'axios';

import MuiTypography from '@material-ui/core/Typography';

import SubCard from '../../ui-component/cards/SubCard';
import {ConfirmDeleteModal} from './ConfirmDeleteModal';
import {ShowConsultations} from './ShowConsultations';

const useStyles = makeStyles({
    table: {
        minWidth: 350
    },
    searchControl: {
        width: '100%',
        paddingRight: '8px',
        paddingLeft: '16px',
        marginBottom: '16px',
        marginTop: '16px'
    }
});

export default function Doctors() {
    const classes = useStyles();
    const BASE_URL = 'https://backend-test.alsahaba.sa/api';
    const [doctors, setDoctors] = React.useState([]);
    const [selectedDoctor, setSelectedUser] = React.useState([]);
    const [value, setValue] = React.useState('');
    const [visible, setVisibility] = React.useState(false);
    const [showDeleteModal, setShowDeleteModal] = React.useState(false);
    const [fileTypes, setFileTypes] = React.useState({});

    const fetchDoctors = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setDoctors(response.data);
        });
    };
    const fetchSearch = async () => {
        await axios.get(`${BASE_URL}/search?query=${value}`).then((response) => {
            setDoctors(response.data);
        });
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const closePopup = () => {
        setVisibility(false);
    };
    const closeDeleteModal = () => {
        setShowDeleteModal(false);
    };
    const showDeletePopUpModal = (patient) => {
        setShowDeleteModal(true);
        setSelectedUser(patient);
    };
    const showSelectedDoctor = (doctor) => {
        setVisibility(true);
        setSelectedUser(doctor);
    };
    const handleChange = (event) => {
        if (event.target.value === '') {
            setValue(event.target.value);
            fetchDoctors();
        } else {
            setValue(event.target.value);
            fetchSearch();
        }
    };
    useEffect(() => {
        const checkFileType = async (doctor, field) => {
            try {
                const img = new Image();
                img.onload = function () {
                    // If the image is successfully loaded, it's an image
                    setFileTypes((prevFileTypes) => ({
                        ...prevFileTypes,
                        [doctor.id]: {
                            ...prevFileTypes[doctor.id],
                            [field]: 'image'
                        }
                    }));
                };
                img.onerror = function () {
                    // If an error occurs during loading, it's not an image
                    setFileTypes((prevFileTypes) => ({
                        ...prevFileTypes,
                        [doctor.id]: {
                            ...prevFileTypes[doctor.id],
                            [field]: 'non-image'
                        }
                    }));
                };
                img.src = doctor[field];
            } catch (error) {
                console.error(`Error checking file type for doctor ${doctor.id}, field ${field}:`, error);
            }
        };

        // Iterate through doctors and check file type for each field
        doctors
            ?.filter((user) => user.is_doctor)
            .forEach((doctor) => {
                const fieldsToCheck = ['copy_of_iqama_number', 'copy_of_scfhs_registration_card', 'cv', 'personal_photo'];

                fieldsToCheck.forEach((field) => {
                    if (!fileTypes[doctor.id] || !fileTypes[doctor.id][field]) {
                        checkFileType(doctor, field);
                    }
                });
            });
    }, [doctors]);
    useEffect(() => {
        fetchDoctors();
    }, []);
    return (
        <React.Fragment>
            {showDeleteModal && <ConfirmDeleteModal patient={selectedDoctor} handleClose={closeDeleteModal} />}
            {visible && <ShowSelectedDoctor doctor={selectedDoctor} handleClose={closePopup} />}
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Card>
                        <Grid container direction="row" justifyContent="space-between" alignItems={'center'}>
                            <Grid item>
                                <CardHeader title="Registered Doctors" />
                            </Grid>
                            <Grid item style={{marginRight: '25px'}}>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-profile"
                                    value={value}
                                    onChange={handleChange}
                                    placeholder="Search doctors"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1.3rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                    aria-describedby="search-helper-text"
                                    inputProps={{
                                        'aria-label': 'weight'
                                    }}
                                    labelWidth={0}
                                />
                            </Grid>
                        </Grid>
                        <Divider />
                        <TableContainer>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>#</TableCell>
                                        <TableCell>Doctor name</TableCell>
                                        <TableCell align="left">ID/Iqama number</TableCell>
                                        <TableCell align="left">Copy of iqama number</TableCell>
                                        <TableCell align="left" style={{whiteSpace: 'nowrap'}}>
                                            Date of birth
                                        </TableCell>
                                        <TableCell align="left">Mobile number</TableCell>
                                        <TableCell align="left">E-mail</TableCell>
                                        <TableCell align="left">Nationality</TableCell>
                                        <TableCell align="left">SCFHS Registration</TableCell>
                                        <TableCell align="left">Copy of scfhs registration card</TableCell>
                                        <TableCell align="left">Current Employer Name</TableCell>
                                        <TableCell align="left">Current Employer License Number</TableCell>
                                        <TableCell align="left">Current Employer City Location </TableCell>
                                        <TableCell align="left">Current Employer Website</TableCell>
                                        <TableCell align="left">CV/Resume</TableCell>
                                        <TableCell align="left">Personal photo</TableCell>
                                        <TableCell align="left">Verifed</TableCell>
                                        <TableCell align="left">Registered date</TableCell>
                                        <TableCell align="left">Last login</TableCell>
                                        <TableCell align="left"></TableCell>
                                        <TableCell align="left"></TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {doctors &&
                                        doctors
                                            ?.filter((user) => user.is_doctor)
                                            .map((doctor, index) => (
                                                <TableRow key={doctor.id}>
                                                    <TableCell align="left">{index + 1}</TableCell>
                                                    <TableCell component="th">{doctor.full_name}</TableCell>
                                                    <TableCell align="left">{doctor.iqama_number}</TableCell>
                                                    <TableCell align="left">
                                                        {/* Check if file type is available for the doctor and field */}
                                                        {fileTypes[doctor.id] &&
                                                            fileTypes[doctor.id]['copy_of_iqama_number'] &&
                                                            (fileTypes[doctor.id]['copy_of_iqama_number'].startsWith('image') ? (
                                                                <a
                                                                    href={doctor.copy_of_iqama_number}
                                                                    target="_blank"
                                                                    rel="noopener noreferrer"
                                                                >
                                                                    Image
                                                                </a>
                                                            ) : (
                                                                <a
                                                                    href={doctor.copy_of_iqama_number}
                                                                    target="_blank"
                                                                    rel="noopener noreferrer"
                                                                >
                                                                    File
                                                                </a>
                                                            ))}
                                                    </TableCell>
                                                    <TableCell align="left">{doctor.date_of_birth}</TableCell>
                                                    <TableCell align="left">{doctor.mobile_number}</TableCell>
                                                    <TableCell align="left">{doctor.email}</TableCell>
                                                    <TableCell align="left">{doctor.nationality}</TableCell>
                                                    <TableCell align="left">{doctor.scfhs_registration}</TableCell>
                                                    <TableCell align="left">
                                                        {fileTypes[doctor.id] &&
                                                            fileTypes[doctor.id]['copy_of_scfhs_registration_card'] &&
                                                            (fileTypes[doctor.id]['copy_of_scfhs_registration_card'].startsWith('image') ? (
                                                                <a
                                                                    href={doctor.copy_of_scfhs_registration_card}
                                                                    target="_blank"
                                                                    rel="noopener noreferrer"
                                                                >
                                                                    Image
                                                                </a>
                                                            ) : (
                                                                <a
                                                                    href={doctor.copy_of_scfhs_registration_card}
                                                                    target="_blank"
                                                                    rel="noopener noreferrer"
                                                                >
                                                                    File
                                                                </a>
                                                            ))}
                                                    </TableCell>
                                                    <TableCell align="left">{doctor.current_employer_name}</TableCell>
                                                    <TableCell align="left">{doctor.current_employer_license_num}</TableCell>
                                                    <TableCell align="left">{doctor.current_employer_city}</TableCell>
                                                    <TableCell align="left">{doctor.current_employer_website}</TableCell>
                                                    <TableCell align="left">
                                                        {fileTypes[doctor.id] &&
                                                            fileTypes[doctor.id]['cv'] &&
                                                            (fileTypes[doctor.id]['cv'].startsWith('image') ? (
                                                                <a href={doctor.cv} target="_blank" rel="noopener noreferrer">
                                                                    Image
                                                                </a>
                                                            ) : (
                                                                <a href={doctor.cv} target="_blank" rel="noopener noreferrer">
                                                                    File
                                                                </a>
                                                            ))}
                                                    </TableCell>
                                                    <TableCell align="left">
                                                        {fileTypes[doctor.id] &&
                                                            fileTypes[doctor.id]['personal_photo'] &&
                                                            (fileTypes[doctor.id]['personal_photo'].startsWith('image') ? (
                                                                <a href={doctor.personal_photo} target="_blank" rel="noopener noreferrer">
                                                                    Image
                                                                </a>
                                                            ) : (
                                                                <a href={doctor.personal_photo} target="_blank" rel="noopener noreferrer">
                                                                    File
                                                                </a>
                                                            ))}
                                                    </TableCell>
                                                    <TableCell align="left">{doctor.is_verified === false ? 'No' : 'Yes'}</TableCell>
                                                    <TableCell align="left">{formatDate(doctor.created_at)}</TableCell>
                                                    <TableCell align="left">
                                                        {doctor.last_login === null ? 'N/A' : formatDate(doctor.last_login)}
                                                    </TableCell>
                                                    <TableCell align="left" onClick={() => showSelectedDoctor(doctor)}>
                                                        <img src="images/eye.png" id="delete-icon" />
                                                    </TableCell>
                                                    <TableCell align="left" onClick={() => showDeletePopUpModal(doctor)}>
                                                        <img src="images/trash-bin.png" id="delete-icon" />
                                                    </TableCell>
                                                </TableRow>
                                            ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Card>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}

const ShowSelectedDoctor = (props) => {
    const BASE_URL = 'https://backend-test.alsahaba.sa/api';
    // const [doctors, setDoctors] = React.useState([]);
    const [userId, setUserId] = React.useState(null);
    const [isImage, setIsImage] = React.useState(false);
    const [showConsultations, setShowConsultations] = React.useState(false);
    const [selectedDoctor, setShowSelectedDoctor] = React.useState([]);
    const closeConsultationsPopUp = () => {
        setShowConsultations(false);
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const handleSubmit = async (id) => {
        var bodyFormData = new FormData();
        bodyFormData.append('is_verified', true);
        axios({
            method: 'put',
            url: `${BASE_URL}/update-user/${id}/`,
            data: bodyFormData,
            headers: {'Content-Type': 'application/json'}
        })
            .then(function (response) {
                window.location.reload(false);
            })
            .catch(function (response) {
                console.log(response);
            });
    };

    const handleSubmitContract = async (e) => {
        e.preventDefault();

        try {
            // Fetch the current user data
            const response = await axios.get(`${BASE_URL}/update-user/${userId}/`);
            const currentUserData = response.data;

            // Create FormData and append the contract file
            const bodyFormData = new FormData();
            if (e.target.contract.files[0] === undefined) {
                bodyFormData.append('contract', null);
            } else {
                bodyFormData.append('contract', e.target.contract.files[0]);
            }

            bodyFormData.append('is_verified', currentUserData.is_verified);

            // Make the PUT request with the updated FormData
            await axios.put(`${BASE_URL}/update-user/${userId}/`, bodyFormData, {
                headers: {'Content-Type': 'multipart/form-data'}
            });

            window.location.reload();
        } catch (error) {
            console.error('Error submitting contract:', error);
            console.log(error);
        }
    };

    const handleSubmitNonObjectionLetter = async (e) => {
        e.preventDefault();

        try {
            // Fetch the current user data
            const response = await axios.get(`${BASE_URL}/update-user/${userId}/`);
            const currentUserData = response.data;

            // Create FormData and append the contract file
            const bodyFormData = new FormData();
            if (e.target.no_objection_letter.files[0] === undefined) {
                bodyFormData.append('no_objection_letter', null);
            } else {
                bodyFormData.append('no_objection_letter', e.target.no_objection_letter.files[0]);
            }

            bodyFormData.append('is_verified', currentUserData.is_verified);

            // Make the PUT request with the updated FormData
            await axios.put(`${BASE_URL}/update-user/${userId}/`, bodyFormData, {
                headers: {'Content-Type': 'multipart/form-data'}
            });

            window.location.reload();
        } catch (error) {
            console.error('Error submitting no_objection_letter:', error);
            console.log(error);
        }
    };
    const handleSubmitAdditionalFiles = async (e) => {
        e.preventDefault();

        try {
            // Fetch the current user data
            const response = await axios.get(`${BASE_URL}/update-user/${userId}/`);
            const currentUserData = response.data;

            // Create FormData and append the contract file
            const bodyFormData = new FormData();
            if (e.target.additional_attachment.files[0] === undefined) {
                bodyFormData.append('additional_attachment', null);
            } else {
                bodyFormData.append('additional_attachment', e.target.additional_attachment.files[0]);
            }

            bodyFormData.append('is_verified', currentUserData.is_verified);

            // Make the PUT request with the updated FormData
            await axios.put(`${BASE_URL}/update-user/${userId}/`, bodyFormData, {
                headers: {'Content-Type': 'multipart/form-data'}
            });

            window.location.reload();
        } catch (error) {
            console.error('Error submitting additional_attachment:', error);
            console.log(error);
        }
    };
    const showSelectedConsultations = (doctor) => {
        setShowConsultations(true);
        setShowSelectedDoctor(doctor);
    };
    useEffect(() => {
        const checkFileType = async (fileUrl) => {
            try {
                return new Promise((resolve, reject) => {
                    const img = new Image();
                    img.onload = function () {
                        // If the image is successfully loaded, it's an image
                        resolve(true);
                    };
                    img.onerror = function () {
                        // If an error occurs during loading, it's not an image
                        resolve(false);
                    };
                    img.src = fileUrl;
                });
            } catch (error) {
                console.error('Error checking file type:', error);
                return false;
            }
        };

        const checkImageType = async () => {
            const copyOfIqamaNumberIsImage = await checkFileType(props.doctor.copy_of_iqama_number);
            const copyOfScfhsRegistrationCardIsImage = await checkFileType(props.doctor.copy_of_scfhs_registration_card);
            const personalPhotoIsImage = await checkFileType(props.doctor.personal_photo);
            const cvIsImage = await checkFileType(props.doctor.cv);

            setIsImage({
                copyOfIqamaNumber: copyOfIqamaNumberIsImage,
                copyOfScfhsRegistrationCard: copyOfScfhsRegistrationCardIsImage,
                personalPhoto: personalPhotoIsImage,
                cv: cvIsImage
            });
        };

        checkImageType();
    }, [props.patient]);

    const displayImg = (imgSrc) => {
        document.getElementById('full-image').src = imgSrc;
        var dImg = document.getElementById('image-viewer');
        dImg.style.display = 'block';
    };
    const hideImg = () => {
        var hImg = document.getElementById('image-viewer');
        hImg.style.display = 'none';
    };

    return (
        <>
            {showConsultations && <ShowConsultations patient={selectedDoctor} handleCloseConsultations={closeConsultationsPopUp} />}
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Card className="popup-container">
                        <CardHeader title="Consultation" />
                        <Divider />
                        <CardContent className="popup-body" id="popupCustom" style={{top: '235px'}}>
                            <Button id="closeBtn" onClick={() => props.handleClose()}>
                                X
                            </Button>
                            <Grid container spacing={gridSpacing}>
                                <Grid item xs={12} sm={6}>
                                    <SubCard title={props.doctor.full_name}>
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            disableElevation
                                            type="submit"
                                            onClick={() => showSelectedConsultations(props.doctor)}
                                        >
                                            Show Consultations
                                        </Button>
                                        <Grid container direction="column" id="poppins" spacing={1}>
                                            <Grid item>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Doctor name: &nbsp;
                                                    <span id="small-text">{props.doctor.full_name}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    ID/Iqama number: &nbsp;
                                                    <span id="small-text">{props.doctor.iqama_number}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Copy of ID/Iqama number: &nbsp;
                                                    {isImage.copyOfIqamaNumber ? (
                                                        <span
                                                            id="small-text"
                                                            onClick={() => displayImg(props.doctor.copy_of_iqama_number)}
                                                            style={{cursor: 'pointer', textDecoration: 'underline', color: 'blue'}}
                                                        >
                                                            Image
                                                        </span>
                                                    ) : (
                                                        <a
                                                            href={props.doctor.copy_of_iqama_number}
                                                            target="_blank"
                                                            rel="noopener noreferrer"
                                                        >
                                                            File
                                                        </a>
                                                    )}
                                                    {isImage.copyOfIqamaNumber && (
                                                        <div id="image-viewer" onClick={hideImg}>
                                                            <span className="closed">&times;</span>
                                                            <img className="modal-content" id="full-image" alt="Image" />
                                                        </div>
                                                    )}
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Date of birth: &nbsp;
                                                    <span id="small-text">{props.doctor.date_of_birth}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Mobile number: &nbsp;
                                                    <span id="small-text">{props.doctor.mobile_number}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    E-mail: &nbsp;
                                                    <span id="small-text">{props.doctor.email}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Nationality: &nbsp;
                                                    <span id="small-text">{props.doctor.nationality}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    SCFHS registration: &nbsp;
                                                    <span id="small-text">{props.doctor.scfhs_registration}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Copy of SCFHS registration : &nbsp;
                                                    {isImage.copyOfScfhsRegistrationCard ? (
                                                        <span
                                                            id="small-text"
                                                            onClick={() => displayImg(props.doctor.copy_of_scfhs_registration_card)}
                                                            style={{cursor: 'pointer', textDecoration: 'underline', color: 'blue'}}
                                                        >
                                                            Image
                                                        </span>
                                                    ) : (
                                                        <a
                                                            href={props.doctor.copy_of_scfhs_registration_card}
                                                            target="_blank"
                                                            rel="noopener noreferrer"
                                                        >
                                                            File
                                                        </a>
                                                    )}
                                                    {isImage.copyOfScfhsRegistrationCard && (
                                                        <div id="image-viewer" onClick={hideImg}>
                                                            <span className="closed">&times;</span>
                                                            <img className="modal-content" id="full-image" alt="Image" />
                                                        </div>
                                                    )}
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Current Employer Name : &nbsp;
                                                    <span id="small-text">{props.doctor.current_employer_name}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Current Employer License Number : &nbsp;
                                                    <span id="small-text">
                                                        {props?.doctor?.current_employer_license_num
                                                            ? props?.doctor?.current_employer_license_num
                                                            : 'N/A'}
                                                    </span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Current Employer City Location : &nbsp;
                                                    <span id="small-text">{props.doctor.current_employer_city}</span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Current Employer Website : &nbsp;
                                                    <a href={props.doctor.current_employer_website} id="small-text">
                                                        {props.doctor.current_employer_website}
                                                    </a>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    CV/Resume: &nbsp;
                                                    <span id="small-text">
                                                        <a href={props.doctor.cv} target="_blank">
                                                            File
                                                        </a>
                                                        {/* {props.doctor.cv} */}
                                                    </span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Personal Photo: &nbsp;
                                                    {isImage.personalPhoto ? (
                                                        <span
                                                            id="small-text"
                                                            onClick={() => displayImg(props.doctor.personal_photo)}
                                                            style={{cursor: 'pointer', textDecoration: 'underline', color: 'blue'}}
                                                        >
                                                            Image
                                                        </span>
                                                    ) : (
                                                        <a href={props.doctor.personal_photo} target="_blank" rel="noopener noreferrer">
                                                            File
                                                        </a>
                                                    )}
                                                    {isImage.personalPhoto && (
                                                        <div id="image-viewer" onClick={hideImg}>
                                                            <span className="closed">&times;</span>
                                                            <img className="modal-content" id="full-image" alt="Image" />
                                                        </div>
                                                    )}
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Verifed: &nbsp;
                                                    <span id="small-text">
                                                        {props.doctor.is_verified === false ? (
                                                            <>
                                                                <span>No </span>
                                                                <Button
                                                                    type="submit"
                                                                    variant="contained"
                                                                    color="success"
                                                                    disableElevation
                                                                    onClick={() => handleSubmit(props.doctor.id)}
                                                                >
                                                                    Verify!
                                                                </Button>
                                                            </>
                                                        ) : (
                                                            'Yes'
                                                        )}
                                                    </span>
                                                </MuiTypography>
                                                <MuiTypography variant="h3" gutterBottom>
                                                    Last login: &nbsp;
                                                    <span id="small-text">
                                                        {props.doctor.last_login === null ? 'N/A' : formatDate(props.doctor.last_login)}
                                                    </span>
                                                </MuiTypography>
                                                <form onSubmit={handleSubmitContract}>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Contract: &nbsp;{' '}
                                                        {props?.doctor?.contract ? (
                                                            <a id="small-text" href={props.doctor.contract}>
                                                                Download doctor's contract.
                                                            </a>
                                                        ) : (
                                                            ''
                                                        )}
                                                        <input type="file" name="contract" onClick={() => setUserId(props.doctor.id)} />
                                                    </MuiTypography>
                                                    <Button type="submit" variant="contained" color="success" disableElevation>
                                                        Upload!
                                                    </Button>
                                                </form>
                                                <form onSubmit={handleSubmitNonObjectionLetter}>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Non Objection Letter: &nbsp;{' '}
                                                        {props?.doctor?.no_objection_letter ? (
                                                            <a id="small-text" href={props.doctor.no_objection_letter}>
                                                                Download doctor's non objection letter.
                                                            </a>
                                                        ) : (
                                                            ''
                                                        )}
                                                        <input
                                                            type="file"
                                                            name="no_objection_letter"
                                                            onClick={() => setUserId(props.doctor.id)}
                                                        />
                                                    </MuiTypography>
                                                    <Button type="submit" variant="contained" color="success" disableElevation>
                                                        Upload!
                                                    </Button>
                                                </form>
                                                <form onSubmit={handleSubmitAdditionalFiles}>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Additional Attachment: &nbsp;
                                                        {props?.doctor?.additional_attachment ? (
                                                            <a id="small-text" href={props.doctor.additional_attachment}>
                                                                Download doctor's additional attachment.
                                                            </a>
                                                        ) : (
                                                            ''
                                                        )}
                                                        <input
                                                            type="file"
                                                            name="additional_attachment"
                                                            onClick={() => setUserId(props.doctor.id)}
                                                        />
                                                    </MuiTypography>
                                                    <Button type="submit" variant="contained" color="success" disableElevation>
                                                        Upload!
                                                    </Button>
                                                </form>
                                            </Grid>
                                        </Grid>
                                    </SubCard>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </>
    );
};
